*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* navbar styling */
.navbar{
  display: flex;
  padding: 20px;
  height: fit-content;
  background: #5C3DC2;
  /* margin-bottom: 50px; */
  justify-content: space-between;
  align-items: center;
  border-bottom: 10px solid rgba(40, 40, 40, 1);
}
.navbar> div{
  width:fit-content;
  height:100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.navbar .logo{
  width:100px;
  height:100px
}
.navbar> div> p{
  color: #B5F04F;
  font-size: 20px;
  font-weight: bold;
}
.navbar> div> p> span{
  color:#F0CE7F
}
.navbar> .links{
  display: flex;
  width: fit-content;
  justify-content: space-around;
  align-items: center;
}
.links> li{
  list-style-type: none;
  margin:0 20px ;
}
.links> li> a{
  text-decoration: none;
  color: rgba(255, 255, 255, 0.8);
  font-size: 18px;
  font-weight: 450;
}
.links> li> a:hover{
  color: rgba(255, 255, 255, 0.5);
}




/* intro page styling */
.intro-page{
  background: #F0CE7F;
  /* padding: 20px; */
}
.intro-page> .section-one{
  /* margin-bottom: 50px; */
  padding: 100px 20px;
  height: 350px;
  /* outline: 2px solid black; */
  overflow:visible;
  /* z-index: 10; */
}
.intro-page> .section-one> h1{
  font-size: 50px;
  display: flex;
  width: 100%;
  max-width: 700px;
  /* margin-bottom: 50px; */
  padding: 20px;
  height: fit-content;
  border-bottom: 5px solid #5C3DC2;
  /* outline: 2px solid black; */
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: auto;
  margin-top: -110px;
  /* z-index: 100; */
  background: white;
}

.intro-page> .section-two {
  /* outline: 2px solid black; */
  padding: 20px;
  background: rgba(40, 40, 40, 0.5);
}
.intro-page> .section-two> main {
  /* outline: 2px solid black; */
  margin: auto;
  width: 100%;
  max-width: 700px;
  border-bottom:5px solid #5C3DC2 ;
  padding: 20px;
  background: white;
  margin-top: -100px;
}

.intro-page> .section-two>main> h1{
  margin-bottom: 20px;
}
.intro-page> .section-two>main> p{
  margin-bottom: 15px;
}
.intro-page> .section-two>main> p> ol{
  list-style-position: inside;
}
.intro-page> .section-two>main> p> ol> li{
  font-weight: bold;
}




.canvas-area{
  min-height:100vh ;
}
.config-area{
  min-height: 50px;
  background: royalblue;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: row wrap;
}
.config-area> * {
  margin-left: 20px;

}
.config-area> .speed> select{
  margin-left: 5px;
  height: 25px;
  border:2px solid navy
} 
.config-area> .number-of-bars> input{
  margin-left: 5px;
  height: 25px;
  border:2px solid navy
}
.config-area> button{
  width: 25px;
  height: 25px;
  border: none;
  border-radius: 50%;
  /* color: transparent; */
  background: transparent;
}
.config-area> button> i{
  color: white;
  transform: scale(1.4);
}
.config-area> button> i:hover{
  color: rgba(255, 255, 255, 0.8);
  /* transform: scale(1.4); */
}
.config-area> .pick-algorithm> select{
  margin-left: 5px;
  height: 25px;
  border:2px solid navy
} 
